<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 企业编码 -->
            <a-form-model-item
              label="企业编码"
              prop="enterpriseCode"
              ref="enterpriseCode"
            >
              <a-input
                v-model="entity.enterpriseCode"
                placeholder="请输入企业编码"
                name="enterpriseCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 企业名称 -->
            <a-form-model-item
              label="企业名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入企业名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属行业 -->
            <a-form-model-item
              label="所属行业"
              prop="industry"
              ref="industry"
            >
              <a-select
                v-model="entity.industry"
                placeholder="请选择所属行业"
                name="industry"
              >
                <a-select-option value="制造业">制造业</a-select-option>
                <a-select-option value="电子信息">电子信息</a-select-option>
                <a-select-option value="汽车制造">汽车制造</a-select-option>
                <a-select-option value="医药制造">医药制造</a-select-option>
                <a-select-option value="食品加工">食品加工</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 企业规模 -->
            <a-form-model-item
              label="企业规模"
              prop="scale"
              ref="scale"
            >
              <a-select
                v-model="entity.scale"
                placeholder="请选择企业规模"
                name="scale"
              >
                <a-select-option value="大型">大型</a-select-option>
                <a-select-option value="中型">中型</a-select-option>
                <a-select-option value="小型">小型</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 创立日期 -->
            <a-form-model-item
              label="创立日期"
              prop="foundDate"
              ref="foundDate"
            >
              <a-date-picker
                v-model="entity.foundDate"
                format="YYYY-MM-DD"
                placeholder="请选择创立日期"
                style="width: 100%"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 员工数量 -->
            <a-form-model-item
              label="员工数量"
              prop="employeeCount"
              ref="employeeCount"
            >
              <a-input-number
                v-model="entity.employeeCount"
                placeholder="请输入员工数量"
                :min="1"
                style="width: 100%"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 注册资本 -->
            <a-form-model-item
              label="注册资本"
              prop="registeredCapital"
              ref="registeredCapital"
            >
              <a-input
                v-model="entity.registeredCapital"
                placeholder="请输入注册资本"
                name="registeredCapital"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 运营状态 -->
            <a-form-model-item
              label="运营状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择运营状态"
                name="status"
              >
                <a-select-option value="运营中">运营中</a-select-option>
                <a-select-option value="筹建中">筹建中</a-select-option>
                <a-select-option value="暂停运营">暂停运营</a-select-option>
                <a-select-option value="已关闭">已关闭</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 地址 -->
            <a-form-model-item 
              label="地址" 
              prop="address"
              ref="address"
            >
              <a-input
                v-model="entity.address"
                placeholder="请输入详细地址"
                name="address"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述 -->
            <a-form-model-item 
              label="企业描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入企业描述"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 联系人信息 -->
      <g-collapse headerName="联系人信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-contact-btn" type="primary" icon="plus" @click="addContact">添加联系人</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="contactColumns" 
                :dataSource="entity.contacts" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="contactName" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.contacts[index].contactName"
                    placeholder="请输入联系人姓名"
                  />
                </template>
                <template slot="position" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.contacts[index].position"
                    placeholder="请输入职位"
                  />
                </template>
                <template slot="phone" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.contacts[index].phone"
                    placeholder="请输入联系电话"
                  />
                </template>
                <template slot="email" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.contacts[index].email"
                    placeholder="请输入电子邮箱"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeContact(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 组织架构 -->
      <g-collapse headerName="组织架构">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-dept-btn" type="primary" icon="plus" @click="addDepartment">添加部门</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="departmentColumns" 
                :dataSource="entity.departments" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="deptName" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.departments[index].deptName"
                    placeholder="请输入部门名称"
                  />
                </template>
                <template slot="deptCode" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.departments[index].deptCode"
                    placeholder="请输入部门编码"
                  />
                </template>
                <template slot="manager" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.departments[index].manager"
                    placeholder="请输入部门主管"
                  />
                </template>
                <template slot="staffCount" slot-scope="text, record, index">
                  <a-input-number
                    v-model="entity.departments[index].staffCount"
                    placeholder="请输入人数"
                    :min="0"
                    style="width: 100%"
                  />
                </template>
                <template slot="description" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.departments[index].description"
                    placeholder="请输入描述"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeDepartment(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 生产基地 -->
      <g-collapse headerName="生产基地">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-facility-btn" type="primary" icon="plus" @click="addFacility">添加生产基地</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="facilityColumns" 
                :dataSource="entity.facilities" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="facilityName" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.facilities[index].facilityName"
                    placeholder="请输入基地名称"
                  />
                </template>
                <template slot="location" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.facilities[index].location"
                    placeholder="请输入基地位置"
                  />
                </template>
                <template slot="area" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.facilities[index].area"
                    placeholder="请输入占地面积"
                  />
                </template>
                <template slot="purpose" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.facilities[index].purpose"
                    placeholder="请输入用途"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeFacility(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
import moment from "moment";

export default {
  name: "CreateOrEditEnterprise",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        enterpriseCode: "",
        name: "",
        industry: "",
        scale: "",
        foundDate: null,
        employeeCount: 0,
        registeredCapital: "",
        address: "",
        description: "",
        contacts: [],
        departments: [],
        facilities: [],
        status: "运营中",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      rules: {
        enterpriseCode: [
          { required: true, message: "请输入企业编码", trigger: "change" },
          { pattern: /^EN\d{4}$/, message: "企业编码格式不正确，应为EN开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入企业名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "企业名称不能包含前后空格", trigger: "change" }
        ],
        industry: [
          { required: true, message: "请选择所属行业", trigger: "change" }
        ],
        scale: [
          { required: true, message: "请选择企业规模", trigger: "change" }
        ],
        foundDate: [
          { required: true, message: "请选择创立日期", trigger: "change" }
        ],
        employeeCount: [
          { required: true, message: "请输入员工数量", trigger: "change" },
          { type: "number", min: 1, message: "员工数量必须大于0", trigger: "change" }
        ],
        registeredCapital: [
          { required: true, message: "请输入注册资本", trigger: "change" }
        ],
        address: [
          { required: true, message: "请输入地址", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择运营状态", trigger: "change" }
        ]
      },
      contactColumns: [
        {
          title: "姓名",
          dataIndex: "contactName",
          key: "contactName",
          width: "20%",
          scopedSlots: { customRender: "contactName" }
        },
        {
          title: "职位",
          dataIndex: "position",
          key: "position",
          width: "20%",
          scopedSlots: { customRender: "position" }
        },
        {
          title: "联系电话",
          dataIndex: "phone",
          key: "phone",
          width: "20%",
          scopedSlots: { customRender: "phone" }
        },
        {
          title: "电子邮箱",
          dataIndex: "email",
          key: "email",
          width: "30%",
          scopedSlots: { customRender: "email" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ],
      departmentColumns: [
        {
          title: "部门名称",
          dataIndex: "deptName",
          key: "deptName",
          width: "20%",
          scopedSlots: { customRender: "deptName" }
        },
        {
          title: "部门编码",
          dataIndex: "deptCode",
          key: "deptCode",
          width: "15%",
          scopedSlots: { customRender: "deptCode" }
        },
        {
          title: "部门主管",
          dataIndex: "manager",
          key: "manager",
          width: "15%",
          scopedSlots: { customRender: "manager" }
        },
        {
          title: "人数",
          dataIndex: "staffCount",
          key: "staffCount",
          width: "10%",
          scopedSlots: { customRender: "staffCount" }
        },
        {
          title: "描述",
          dataIndex: "description",
          key: "description",
          width: "30%",
          scopedSlots: { customRender: "description" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ],
      facilityColumns: [
        {
          title: "基地名称",
          dataIndex: "facilityName",
          key: "facilityName",
          width: "20%",
          scopedSlots: { customRender: "facilityName" }
        },
        {
          title: "位置",
          dataIndex: "location",
          key: "location",
          width: "25%",
          scopedSlots: { customRender: "location" }
        },
        {
          title: "占地面积",
          dataIndex: "area",
          key: "area",
          width: "15%",
          scopedSlots: { customRender: "area" }
        },
        {
          title: "用途",
          dataIndex: "purpose",
          key: "purpose",
          width: "30%",
          scopedSlots: { customRender: "purpose" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取企业数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        enterpriseCode: "",
        name: "",
        industry: "",
        scale: "",
        foundDate: null,
        employeeCount: 0,
        registeredCapital: "",
        address: "",
        description: "",
        contacts: [],
        departments: [],
        facilities: [],
        status: "运营中",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        
        // 处理日期
        let foundDate = null;
        if (row.foundDate) {
          foundDate = moment(row.foundDate);
        }
        
        // 初始化联系人数组
        let contacts = [];
        if (row.contact) {
          // 如果有联系人字段，创建默认联系人
          contacts.push({
            id: Date.now(),
            contactName: "默认联系人",
            position: "管理员",
            phone: row.contact,
            email: "contact@example.com"
          });
        }
        
        // 初始化部门和生产基地为空数组，等待用户添加
        let departments = [];
        let facilities = [];
        
        this.entity = {
          ...this.entity,
          ...row,
          foundDate,
          contacts,
          departments,
          facilities,
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 添加联系人
    addContact() {
      this.entity.contacts.push({
        id: Date.now(),
        contactName: "",
        position: "",
        phone: "",
        email: ""
      });
    },
    
    // 删除联系人
    removeContact(index) {
      this.entity.contacts.splice(index, 1);
    },
    
    // 添加部门
    addDepartment() {
      this.entity.departments.push({
        id: Date.now(),
        deptName: "",
        deptCode: `DEPT${this.entity.departments.length + 1}`,
        manager: "",
        staffCount: 0,
        description: ""
      });
    },
    
    // 删除部门
    removeDepartment(index) {
      this.entity.departments.splice(index, 1);
    },
    
    // 添加生产基地
    addFacility() {
      this.entity.facilities.push({
        id: Date.now(),
        facilityName: "",
        location: "",
        area: "",
        purpose: ""
      });
    },
    
    // 删除生产基地
    removeFacility(index) {
      this.entity.facilities.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-contact-btn,
.add-dept-btn,
.add-facility-btn {
  margin-bottom: 16px;
}
</style> 